html * {
  outline: 1px solid red;
}

html, body {
  padding: 0;
  margin: 0;
}
.card_container {
  display: flex;
  flex-wrap: wrap;
  }

.card-item {
  border: 1px solid grey;
  padding: 10px;
  width: 20%;
  height: 200px;
  box-sizing: border-box;
  }

.card-content {
  background-color: green;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  }

@media screen and (max-width: 1024px){
  .card-item {
    width: 33.3%;
  }
}
@media screen and (max-width: 768px){
  .card-item {
    width: 50%;
  }
}
@media screen and (max-width: 420px){
  .card-item {
    width: 100%;
  }
}

